<template>
  <div id="app">
    <div id="title-wrapper">
      <h1 id="title"><u>Chatbot for <br> SlovenskÃ¡ SporiteÄ¾Åˆa</u></h1>
      <div style="margin-top: 50px;"></div>
      <p id="t_par">Press the <img src="arrow-up.svg"> key to start/progress the dialogue</p>
    </div>
    
    <VueBotUI
      :messages="data"
      :options="botOptions"
      v-on:msg-send="messageSendHandler"
      :is-open="true"
      :bot-typing="visual_typing"
    />
  </div>
</template>

<script>
import { VueBotUI } from 'vue-bot-ui'

export default {
  name: 'App',
  components: {
    VueBotUI
  },
  methods: {
    messageSendHandler(msg) {
      this.data.push(msg);

      if (msg.action == "postback") {
        this.resolve_postback(msg.value);
        this.postback_tried = true;
      } else {
        this.bot_typing_msg("Unfortunately, my brain legally belongs to SlovenskÃ¡ SporiteÄ¾Åˆa. I am only a hard-coded demo and I cannot actually respond to your query.")
        this.bot_typing_msg("To progress the demo, press the right arrow key!");
      }
    },
    left() {
      this.nav(-1);
    },
    right() {
      this.nav(1);
    },
    resolve_postback(postback) {
      
      let postback_handler = `postback_${postback}`
      if (!Object.prototype.hasOwnProperty.call(this, postback_handler)) {
        this.bot_typing_msg("Unfortunately an error occured in my code's logic. I apologize for the inconvience but this portfolio website is still in development.")
        return;
      }

      this[postback_handler]();
    }, 
    nav(direction) {


      this.frame += direction;
      
      if (this.frame > this.frame_max) {
        this.frame = this.frame_max;
        return;
      }
      if (this.frame < 0) {
        this.frame = 0;
        return;
      }

      let int_frame = Math.round(this.frame);
      let handler_name = `frame_${int_frame}`;
      
      this[handler_name]();
      
    },
    options_builder(text) {
      this.options = [];
      this.options_text = text;
      return this;
    },
    url(text, url) {
      this.options.push({
        text: text,
        value: url,
        action: "url"
      })
      return this;
    },
    postback(text, postback) {
      this.options.push({
        text: text,
        value: postback,
        action: "postback"
      })
      return this;
    },
    get_options() {
      return {
        agent: 'bot',
        type: 'button',
        text: this.options_text,
        disableInput: true,
        options: this.options
      }
    },
    bot_msg(msg_string) {
      this.data.push({
            agent: 'bot',
            type: 'text',
            text: msg_string,
            disableInput: false
      });
    },
    bot_options(options) {
      this.data.push(options);
    },
    user_msg(msg_string) {
      this.data.push({
            agent: 'user',
            type: 'text',
            text: msg_string,
            disableInput: false
      });
    },
    user_typing_promise(msg_string) {
      return [new Promise(acc => {
        setTimeout(() => {
          this.user_msg(msg_string);
          acc();
        }, 300);
      }), false];
    },
    bot_typing_promise(msg_string, timeout) {
      return [new Promise(acc => {
        setTimeout(() => {
          this.bot_msg(msg_string);
          acc();
        }, timeout);
      }), true];
    },
    bot_options_promise(options, timeout) {
      return [new Promise(acc => {
        setTimeout(() => {
          this.bot_options(options);
          acc();
        }, timeout);
      }), true];
    },
    task_resolver() {
      if (this.typing) {
        return;
      }

      if (this.msg_q.length === 0) {
        return;
      } 

      let task, visual_typing, promise, ctx;
      task = this.msg_q.shift();
      ctx = this;

      [promise, visual_typing] = task();

      this.typing = true;
      this.visual_typing = visual_typing;
      promise.then(() => {
        ctx.typing = false;
        ctx.visual_typing = false;

        ctx.task_resolver();
      });
    },
    user_typing_msg(msg_string) {
      let ctx = this;

      let current_task = () => {
          return ctx.user_typing_promise(msg_string);
      }

      this.msg_q.push(current_task);

      this.task_resolver();
    },
    bot_typing_options(options, timeout=null) {
      let ctx = this;

      if (timeout == null) {
        timeout = options.text.length * 10;
      }

      let current_task = () => {
          return ctx.bot_options_promise(options, timeout);
      }

      this.msg_q.push(current_task);

      this.task_resolver();
    },
    bot_typing_msg(msg_string, timeout=null) {
      let ctx = this;

      if (timeout == null) {
        timeout = msg_string.length * 10;
      }

      let current_task = () => {
          return ctx.bot_typing_promise(msg_string, timeout);
      }

      this.msg_q.push(current_task);

      this.task_resolver();
    },
    frame_0() {
      this.bot_typing_msg("Ciao!", 1000);
      this.bot_typing_msg("I was developed to help the HR department with the internal communication.");
      this.bot_typing_msg("My job was to answer commonly asked questions that were related to sick-leaves, holidays, payrolls, and many more topics!");
    },
    frame_1() {
      this.user_typing_msg("Okay, and?");
      this.bot_typing_msg("I was able to answer gramatically incorrect/incomplete messages.")
      this.bot_typing_msg("If the employee asked:")
      this.user_typing_msg("'how t accs my payrooll'")
      this.bot_typing_msg("I would understand the employee's intent and respond:")
      this.bot_typing_options(this.options_builder("You can access your payroll on our website by clicking the button").url("Google 'payroll'", "https://www.google.com/search?q=payroll").get_options())

    },
    frame_2() {
      this.user_typing_msg("What else?");
      this.bot_typing_msg("Some dialogues would be too complicated to handle via texting.")
      this.bot_typing_msg("If you would ask:")
      this.user_typing_msg("I want to ask for a raise")
      this.bot_typing_msg("I would start an interactive dialogue:")
      this.bot_typing_options(
        this.options_builder(
          "Salary raise can be given due to the following reasons:"
        ).postback(
          "Strong job performance", "raise_performance"
        ).postback(
          "Yearly raise", "raise_year"
        ).postback(
          "Underpaid position", "raise_underpaid"
        ).get_options()
      )      
    },
    frame_3() {
      if (!this.postback_tried) {
        this.bot_typing_msg("Try clicking on one of the buttons above!")
        this.postback_tried = true;
        this.frame = 2;
        return;
      }
      
      this.bot_typing_options(
        this.options_builder("I was developed by retraining the fasttext model on a slovak-language dataset.").url("About fasttext", "https://fasttext.cc").get_options()
      );

      this.bot_typing_options(
        this.options_builder("The same technology can be used to solve your problem with grading your open-ended answers!").postback("Show me how!", "show_how").get_options()
      );
    },
    postback_show_how() {
      window.__common.e_keydown({key: "ArrowDown"});
    },
    postback_raise_underpaid() {
      this.bot_typing_options(
        this.options_builder(
          "Why do you feel that you job is underpaid?"
        ).postback(
          "I have recieved a higher salary offer", "higher_salary"
        ).postback(
          "My position is usually valued more", "higher_market"
        ).postback(
          "I am overqualified for the job", "overqualified"
        ).get_options()
      )      
    },
    postback_higher_market() {
      this.bot_typing_msg("Schedule a meeting with your manager about the salary raise and come prepared with the analysis of the job market.")
    },
    postback_higher_salary() {
      this.bot_typing_msg("Feel free to take the other job position if it pays better ðŸ˜ ");
    },
    postback_overqualified() {
      this.bot_typing_options(
        this.options_builder(
          "Please take a look at other open positions we offer. We prioritize our own employees over other candidates."
        ).url(
          "Open positions", "https://growni.sk"
        ).get_options()
      )      
    },
    postback_raise_year() {
      this.bot_typing_options(
        this.options_builder(
          "To get a yearly raise requires you need to:"
        ).url(
          "Compile an attendance sheet", "https://www.google.com/sheets/about/", 
        ).url(
          "Fill in this form", "https://www.google.com/forms/about/"
        ).get_options()
      )      
    },
    postback_raise_performance() {
      this.bot_typing_options(
        this.options_builder(
          "In order to persuade the managment that you have an excellent performance it is good to come prepared with the following documents:"
        ).url(
          "A summary of your projects", "https://dribbble.com/tags/programmer%20portfolio"
        ).postback(
          "A recommendation letter", "reco_letter"
        ).url(
          "An attandance sheet", "https://www.google.com/sheets/about/", 
        ).get_options()
      )      
    },
    postback_reco_letter() {
      this.bot_typing_msg("Ask one of your colleagues that you have worked with before to describe your contribution to the project.");
    }
  },
  mounted() {
    window.chat_ = this;
    window.init_();
  },
  data () {
    return {
      data: [],
      botOptions: {
        botTitle: "Durko chatbot",
        botAvatarImg: "jurko.png",
        botAvatarSize: 34,
      },
      promise_timeout: null,
      frame_max: 3,
      frame: -1,
      msg_q: [],
      typing: false,
      visual_typing: false,
      options_text: "",
      postback_tried: false,
      options: []
  }
}
}
</script>
<style>

#title-wrapper {
  position: absolute;
  left: 20px;
  color: white;
  max-width: 40%;
}

#t_par {
  font-size: 28pt;
}

#t_par img {
  width: 30pt;
  height: 30pt;
  position: relative;
  top: 5px;
  transform: rotate(90deg);
}

