import Vue from 'vue'
import App from './App.vue'

Vue.config.productionTip = false

function e_keydown(e) {
  let k = e.key;

  if (k === "ArrowLeft") {
    window.chat_.left();
  }
  if (k === "ArrowRight") {
    window.chat_.right();
  }
}

window.nav_listener = e_keydown;

window.init_ = function () {
  const g_bubble =  document.querySelector(".qkb-bot-bubble");
  const g_chat =  document.querySelector(".qkb-board");
  const g_wrapper = document.querySelector(".qkb-bot-ui");

  g_bubble.style.display = "none";
  g_wrapper.style.width = "100vw";
  g_wrapper.style.height = "100vh";
  g_wrapper.style.bottom = "0";
  g_wrapper.style.right = "0";
  g_wrapper.style.position = "absolute";
  g_chat.style.position = "relative";
  g_chat.style.left = "50vw";
  g_chat.style.top = "50vh";
  g_chat.style.transform = "translate(-10%, -50%)";

}

window.chat_ = null;

new Vue({
  render: h => h(App),
}).$mount('#app')




